.list {
    width: 320px;
    display: flex;
    font-family: 'Dosis', sans-serif;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
    margin: -20px;
    min-height: 450px;
}

.item {
    max-width: 200px;
    margin: 15px;
}

.photo {
    height: 300px;
    width: 200px;
    margin-right: 10px;
    border-radius: 8px;
    overflow: hidden;
    object-fit: cover;
}

.name {
    font-weight: bold;
    font-size: 20px;
}

.character {
    font-style: italic;
}

@media (min-width: 767px) {
    .list {
        width: 760px;
    }
}

@media (min-width: 940px) {
    .list {
        width: 940px;
    }
}

@media (min-width: 1024px) {
    .list {
        width: 1024px;
    }
}